@import "@/assets/global.scss";







































































































































































.main-content {
  padding-top: 92px;
  margin-top: 16px;
  padding-left: 16px;
  padding-right: 16px;

  > div {
    width: 100%;
    height: 120px;
    background: #FFFFFF;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
    border-radius: 12px;
    padding: 16px;
    margin-bottom: 16px;

    > div {
      text-align: left;
    }
  }
}

.tab-panel {
  width: 100%;
  position: fixed;
  top: 52px;
  background-color: #FFFFFF;

  > div {
    width: 100%;
    max-width: $max-width;
    height: 40px;
    display: flex;
    overflow-x: auto;
    margin: 0 auto;
    padding: 0 16px;
    box-sizing: border-box;
    user-select: none;

    > div {
      @extend .font-14-normal;
      padding-top: 9px;
      margin-right: 12px;
      white-space: nowrap;
    }

    > .selected {
      @extend .font-14-bold;
      border-bottom: 3px solid #FCA311;
    }
  }
}

.icon-panel {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.icon-item {
  width: 54px;
  margin: 6px;
  user-select: none;

  > div:first-child {
    width: 48px;
    height: 48px;
    position: relative;
    margin-bottom: 6px;

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}
